  transition: 0.4s;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
}

.wrapper {
  min-height: 100vh;
  font-weight: 300;
  color: #fff;
  position: relative;
  background-color: #16161a;
}

section {
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #16161a;
  overflow-x: hidden !important;
}
.section_illustration {
  width: 40vw;
}
.emoji {
  width: 30px;
  margin-right: 10px !important;
}
.site-container {
  width: 95vw;
  padding-right: 15px;
