  min-height: 280px;
}
.site-header {
  background-color: #16161a;
}

/*=====================
    2. TechFolio Logo Css
==========================*/

.logo {
  font-size: 2rem;
  letter-spacing: 2px;
  margin: 1rem 0;
  font-style: italic;
  position: fixed;
}
.logo--stroke {
  -webkit-text-stroke-width: 2px;
  -moz-text-stroke-width: 2px;
  -webkit-text-stroke-color: #ffffff;
  -moz-text-stroke-color: #ffffff;
  color: transparent;
}
.logo--shadow {
