}

/*=====================
    3. TechFolio Banner Css
==========================*/
.banner-content {
  text-align: left;
  margin-bottom: 15px;
}
.banner-content p {
  color: #94a1b2;
  font-size: 18px;
  font-weight: 400;
  padding-right: 17px;
}
.banner-content h1 {
  color: #fffffe;
  margin-top: -45px;
  margin-bottom: 30px;
  font-size: 60px;
  line-height: 76px;
}
.banner-content .banner_btns {
