  font-weight: 400;
  letter-spacing: -1px;
  margin-right: 10px;
}
.banner-content .btn-1:hover {
  box-shadow: 0 3px 10px #7f5af0 !important;
}

.wrap-image,
.wrap-default {
  position: relative;
}

.wrap-image img{
  max-width: 100% !important;
}

.wrap-default img {
  width: 90vh !important;
  margin-right: -200px;
}
.wrap-image::before,
.wrap-image::after,
.wrap-default::before,
.wrap-default::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  border: 2px solid #7f5af0;
  transition: all 0.25s ease-out;
}
.wrap-image::before,
.wrap-default::before {
  background-color: #7f5af0;
  top: -15px;
  left: -30px;
}
.wrap-image::after,
.wrap-default::after {
  bottom: -15px;
  left: 30px;
}
.wrap-image:hover::before,
.wrap-default:hover::before {
  top: 15px;
  left: 30px;
}
.wrap-image:hover::after,
.wrap-default:hover::after {
  bottom: 15px;
  left: -25px;
}

/*=====================
    4. TechFolio Navber Css
==========================*/
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: hidden;
  transition: opacity 0.35s, visibility 0.35s, width 0.35s;
  z-index: 50;
}
.overlay:before {
  content: "";
  background: #7f5af0;
  left: -55%;
  top: 0;
  width: 50%;
  height: 100%;
  position: absolute;
  transition: left 0.35s ease;
}
.overlay:after {
  content: "";
  background: #7f5af0;
  right: -55%;
  top: 0;
  width: 50%;
  height: 100%;
  position: absolute;
  transition: all 0.35s ease;
}
.overlay.open {
  opacity: 0.9;
  visibility: visible;
  height: 100%;
}
.overlay.open:before {
  left: 0;
}
.overlay.open:after {
  right: 0;
}
.overlay.open li {
  -webkit-animation: fadeInRight 0.5s ease forwards;
          animation: fadeInRight 0.5s ease forwards;
  -webkit-animation-delay: 0.35s;
          animation-delay: 0.35s;
}
.overlay.open li:nth-of-type(2) {
  -webkit-animation-delay: 0.45s;
          animation-delay: 0.45s;
}
.overlay.open li:nth-of-type(3) {
  -webkit-animation-delay: 0.55s;
          animation-delay: 0.55s;
}
.overlay.open li:nth-of-type(4) {
  -webkit-animation-delay: 0.65s;
          animation-delay: 0.65s;
}
.overlay nav {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 50px;

  font-weight: 400;
  text-align: center;
  z-index: 100;
}
.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%;
}
.overlay ul li {
  display: block;
  height: 20%;
  height: calc(100% / 7);
  min-height: 50px;
  position: relative;
  opacity: 0;
}
.overlay ul li p {
  display: block;
  position: relative;
  color: #ffffff;
  text-decoration: none;
  overflow: hidden;
}
.overlay ul li p:hover:after,
.overlay ul li p:focus:after,
.overlay ul li p:active:after {
  width: 100%;
}
.overlay ul li p:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #ffffff;
  transition: 0.35s;
}

@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

/*=====================
    5. TechFolio Side Navber Css
==========================*/
.side_nav_heading {
  font-size: 2rem;
}

.nav__wrapper {
  overflow: hidden !important;
  position: fixed;
  height: 100%;
  padding: 0px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-end;
  z-index: 100;
}

.nav {
  margin: 0 0 200px 20px;
}

.nav__counter {
  font-size: 22px;
  transition: all 0.15s ease-out;
}

.nav__title {
  font-size: 1.5rem;
  font-weight: 300;
  margin: 0 0 1rem;
  overflow: hidden;
  opacity: 0;
  transition: height 0.3s ease-out, opacity 0.2s ease-out;
  letter-spacing: 2px;
  font-style: italic;
  position: fixed;
}
.nav__title--stroke {
  -webkit-text-stroke-width: 2px;
  -moz-text-stroke-width: 2px;
  -webkit-text-stroke-color: #ffffff;
  -moz-text-stroke-color: #ffffff;
  color: transparent;
}
.nav__title--shadow {
  text-shadow: 6px 6px #7f5af0;
}

.nav li {
  position: relative;
  transition: all 0.3s ease-out;
  margin-bottom: 2.2rem;
  list-style: none;
}
.nav li::after {
  content: "";
  display: block;
  border-left: 2px solid #72757e;
  border-top: 2px solid #72757e;
  height: 250px;
  width: 20px;
  position: absolute;
  left: -30px;
  top: 15px;
}
.nav li a {
  display: block;
  padding: 0;
  color: #fff;
  transition: all 0.15s ease-out;
}
.nav li a:hover {
  background-color: transparent;
  padding-left: 1em;
}
.nav li a:focus {
  background-color: transparent;
}
.nav li.active {
  pointer-events: none;
  padding-left: 1em;
}
.nav li.active:after {
  width: 35px;
  height: 400px;
  top: 35px;
}
.nav li.active .nav__counter {
  font-size: 36px;
}
.nav li.active .nav__title {
  height: 40px;
  opacity: 1;
  overflow: visible;
}
.nav li.active .nav__body {
  height: 100px;
  opacity: 1;
  overflow: visible;
}

