/*=====================
    About Section Css


==========================*/

.about_section {
  font-size: 16px;
}
.about_section .container {
  text-align: center;
}
.about_section .container .device {
  display: inline-block;
  margin: 0 auto;
}
.about_section .container .device .frame {
  padding: 30px 2px 2px;
  border-radius: 10px;
  background: #ddd;
  box-shadow: 0 0 0 1px #ccc;
}
.about_section .container .device .frame .window-content {
  padding: 30px 10px 0px 3px;
  text-align: start;
  font-size: 16px;
}
.about_section .container .device .frame .window-content .line {
  font-size: 18px;
  color: #ffffff;
}
.about_section .container .device .frame .window-content span {
  font-size: 18px;
}

.line span {
  color: #7f5af0;
}

.screen {
  position: relative;
  height: 430px;
  width: 740px;
  border-radius: 0 0 10px 10px;
  background: #242629;
  box-shadow: inset 0 0 0 1px #242629;
}
.screen::before,
.screen::after {
  position: absolute;
  margin-left: -16px;
  content: ' ';
  background: #16161a;
  border-radius: 5px;
  box-shadow: 0 0 0 1px #eee;
}
.screen::before {
  width: 10px;
  height: 10px;
  top: -20px;
  left: 20px;
  background: #ff6464;
  box-shadow: 15px 0 #64ff64, 30px 0 #ffff64;
}
.screen::after {
  width: 90%;
  height: 16px;
  top: -23px;
  left: 10%;
  border-radius: 5px;
  background: #eee;
}
.container,
.device,
.frame,
.screen {
  transition: all 0.5s ease;
}

